export default {
	data() {
		return {
			event_list:'',
			get_img:this.$get_img
		}
	},
	onLoad() {
		this.get_event()
	},
	methods: {
		get_event(){
			this.$api.http.get('article/type_article?type=4').then(res=>{
				this.event_list = res.data
			})
		},
		jump_article(id){
			uni.navigateTo({
				url: '/pages/article/article?id='+id
			});
		}
	},
	onPullDownRefresh() {
		//this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
